@import url(https://fonts.googleapis.com/css?family=Lacquer&display=swap);
html, body {
  margin: 0;
  height: 100%;
  overflow: hidden;
}

.canvasWrapper {
  width:100%;
  height:100%;
  overflow:hidden;
  position: fixed;
  cursor: none;
}

div#title{
  -webkit-text-stroke: thin;
  -webkit-text-stroke-color: white;
  -webkit-filter: invert(1);
          filter: invert(1);
  color: black;
  font-family: 'Lacquer', sans-serif;
  position: fixed;
  pointer-events: none;
  width: 100%;
  text-align: center;
  display: block;
  font-size: 3em;
}

h1#titleText{
  margin: 0;
  pointer-events: none;
}

#pageElems{
  pointer-events: none;
  position: fixed;
  bottom: 0%;
  display: flex;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

.pageItem{
  align-self: center;
}

.pageElemsActive{

}

button{
  pointer-events: fill;
  background-color: black;
  border: none;
  color: white;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: medium;
  outline: none;
  -webkit-transition: 1.5s all;
  transition: 1.5s all;
}

button:hover{
  -webkit-filter: invert(1);
          filter: invert(1);
}

.showP{
  visibility: visible;
  display: block;
  -webkit-transition: 1.5s all;
  transition: 1.5s all;
  background-color: white;
  align-items: center;
  height: 100%;
}

p{
  -webkit-transition: 1.5s all;
  transition: 1.5s all;
  visibility: collapse;
  display: none;
  pointer-events: none;
  margin: 0;
}

.hide{
  visibility: collapse;
  display: none;
}

.pageMode{
  position: fixed;
  top: 0%;
}

a{
  pointer-events: all;
}

